import React, { Component } from "react";
import * as HttpUtils from "./HttpUtils";
import { HttpConstants, Endpoints } from "./constants";

class Input extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inputVal: ""
    };
    this.changeHandler = this.changeHandler.bind(this);
  }

  changeHandler(e) {
    this.props.parentFunction(e.target.value);
  }

  render() {
    return (
      <div>
        <label>{this.props.labelName}</label>
        <input
          type={this.props.inputType}
          id={this.props.id}
          onChange={this.changeHandler}
        />
      </div>
    );
  }
}

export default class LoginForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
    };
    this.clickHandler = this.clickHandler.bind(this);
    this.setUsername = this.setUsername.bind(this);
    this.setPassword = this.setPassword.bind(this);
  }

  setUsername(username) {
    this.setState({ username: username });
  }

  setPassword(password) {
    this.setState({ password: password });
  }

  clickHandler() {
    let url = HttpConstants.LOCALHOST + window.location.port + Endpoints.LOGIN;
    let body = {
      username: this.state.username,
      password: this.state.password
    };

    HttpUtils.post(url, body)
      .then(response => response.json())
      .then(config => {
        alert(config);
      })
      .catch(error => console.log(error));
  }

  render() {
    return (
      <div>
        <Input
          id="username"
          labelName="Username: "
          inputType="text"
          parentFunction={this.setUsername}
        />
        <Input
          id="password"
          labelName="Password: "
          inputType="password"
          parentFunction={this.setPassword}
        />
        <button onClick={this.clickHandler}>{this.props.buttonName}</button>
      </div>
    );
  }
}



